legend { display: none; }
h1, h2, h3, h4, h5, h6 { font-size: 100%; }
table { width: 100%; border-collapse: collapse; table-layout: fixed; } 
.blind { position: absolute; width: 1px; height: 1px; overflow: hidden; text-indent: -99999px; }





